@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Flex:opsz,wght@8..144,100..1000&display=swap');
$pFont: "Roboto Flex", sans-serif;
$mFont: 'Bebas Neue', monospace;
$backgroundColor: #f1f1f1;
$c1: #9fef00;

* {
  font-family: $mFont;
  font-weight: normal;
  color: #1b1b1b;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  max-width: 1440px;
  background: $backgroundColor;
  margin: auto;
}
span {
  color: #9fef00;
}
p {
  font-family: $pFont;
  font-weight: normal;
  font-style: normal;
  font-size: 17px; //responsive
  line-height: 27px; //responsive
}
h1 {
  font-size: 100px; //responsive
  margin: 0;
} 
h2 {
  font-size: 60px; //responsive
  margin: 0;
}
h3 {
  font-size: 22px; //responsive
  margin: 0;
}
h4 {
  margin: 0;
}
a {
  font-size: 23px; //responsive
  color: #f1f1f1;
  z-index: 1;
  text-decoration: none;
  transition: .3s;
}

button {
  background: rgba(0, 0, 0, 0);
  border: 1px solid #f1f1f1;
  padding: 10px 20px; //responsive
  position: relative;
  cursor: pointer;
  transition: .3s;
  a {
    width: 100%;
    height: 100%;
    &:before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: calc(100% + 3px);
      border-right: 1px solid #f1f1f1;
      border-bottom: 1px solid #f1f1f1;
      display: block;
      transform: translate(6px, 6px); //responsive
      z-index: -1;
      transition: .5s;
    }
  }
    &:hover {
      background: rgba(159, 239, 0, 1);
      border: 1px solid #9fef00;
      a {
        &:before {
          transform: translate(0px, 0px);
          height: 100%;
          border-right: 1px solid transparent;
          border-bottom: 1px solid transparent;
        }
      }
    }
}


// Navigation Bar
.sec_Nav {
  width: 100%;
  display: flex;
  padding: 10px 40px; //responsive
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 3;
  transition: .3s;
}

.sbl_Navl {
  width: 25%; //responsive
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    max-width: 100%;
  }
}

.sbl_Navr {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  ul {
    list-style: none;
    margin: 0;
    text-align: right;
  } li {
    display: inline;
    padding-right: 50px; //responsive
    font-size: 22px; //responsive
    a {
      color: #f1f1f1;
    }
  }
}

.sbl_Main {
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.sbl_sec1 {
  width: 100%;
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  padding: 0 50px; //responsive
  overflow: hidden;

  .sbl_swiperCont {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
  }

  .swiper-slide {
    position: relative;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: relative;
      filter: brightness(70%) contrast(100%);
      animation: sbl_scaling 8s infinite;
    }

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: radial-gradient(circle, rgba(2,0,36,0) 0%, rgba(1,112,152,0) 70%, rgb(0, 0, 0) 100%);
    }
  }

  .sbl_Text {
    padding: 0 10px ; //responsive
    padding-top: 100px;
    margin: 0;
    z-index: 1;
    width: 40%; //responsive
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 50px; //responsive
    span {
      color: $c1;
    } h1, p, h3 {
      color: #f1f1f1;
    } h1 {
      line-height: 90%; //responsive
    }
  }
}

.sbl_sec2 {
  background: url("/assets/img/border.png");
  background-size: 40%;
  background-repeat: no-repeat;
  background-position: right;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  padding: 0px 50px; //responsive
  padding-top: 50px; //responsive
  padding-bottom: 120px; //responsive
  align-items: flex-start;
  button {
    border: 1px solid #1b1b1b;
    a {
      color: #1b1b1b;
      &:before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: calc(100% + 3px);
        border-right: 1px solid #1b1b1b;
        border-bottom: 1px solid #1b1b1b;
        display: block;
        transform: translate(6px, 6px); //responsive
        z-index: -1;
        transition: .5s;
      }
    }
    &:hover {
      border: 1px solid #9fef00;
      a {
        color: #f1f1f1;
        &:before {
          transform: translate(0px, 0px);
          height: 100%;
          border-right: 1px solid transparent;
          border-bottom: 1px solid transparent;
        }
      }
    }
  }
}
.sbl_sec2L {
  width: 80%; //responsive
  display: flex;
  flex-direction: column;
  position: relative;
  img {
    width: 100%;
    box-shadow: 15px 20px 10px rgba(0, 0, 0, 0.4);
  }
  .img2 {
    position: absolute;
    top: 300px; //responsive
    width: 75%; //responsive
    left: 300px; //responsive
  }
}
.sbl_sec2R {
  width: 100%;
  padding: 0 50px;
  .lT {
    display: flex;
    align-items: center;
    hr {
      flex-grow: 1;
      height: 1px; 
      background-color: black; 
      margin-right: 10px;
    }
  }
  h3, h2 {
    margin: 0;
  } h2 {
    line-height: 100%; //responsive
    padding: 30px 0;
  }
  div {
    
    button {
      margin-top: 20px;
      margin-left: 170px;
    }
    p {
      line-height: 26px; //responsive
      padding-left: 170px; //responsive
    }
    
  }
  .tStroke {
    color: transparent !important; /* Atur warna teks transparan */
    -webkit-text-stroke-width: 2px; /* Atur lebar outline */
    -webkit-text-stroke-color: rgb(27, 27, 27); /* Atur warna outline */
    -webkit-text-fill-color: transparent;
  }
  .tShadow {
    width: 100%;
    position: relative;
      &:before {
        content: 'Wild Currents';
        position: absolute;
        top: 15px;
        left: 4px;
        color: #1b1b1b;
        width: 100%;
        -webkit-text-stroke-width: 2px;
        -webkit-text-stroke-color: rgb(27, 27, 27);
        -webkit-text-fill-color: #1b1b1b;
        z-index: -1;
      }
  }
}

.sbl_sec3 {
  width: 100%;
  
  padding: 50px 0; //responsive
}
.sbl_sec3T {
  padding: 0 50px; //responsive
  display: flex;
  align-items: center;
  div:nth-child(1) {
    width: 60%; //responsive
  }
}
.sbl_pack {
  width: 100%;
  cursor: grab;
  .ss {
    color: #f1f1f1;
    padding: 10px 50px; //responsive
    padding-bottom: 50px;
    width: 70%; //responsive
    height: 550px; //responsive
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    position: relative;
    row-gap: 20px; //responsive
    h2, p, h3 {
      color: #f1f1f1;
      margin: 0;
    }
    p {
      width: 50%; //responsive
    } 
    .type {
      font-size: 25px; //responsive
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      filter: brightness(50%) contrast(100%);
      z-index: -1;
    }
  }
  .sbl_ico {
    width: 100%;
    display: flex;
    column-gap: 50px; //responsive
    div {
      display: flex;
      align-items: center;
      column-gap: 20px; //responsive
    }
    img {
      position: relative;
      width: 60px;
      filter: brightness(100%) contrast(100%);
    }
  }
}

.sbl_sec4 {
  width: 100%;
  background: #1b1b1b;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 50px 50px; //responsive
  h2 {
    color: #f1f1f1;
    margin: 0;
  } p {
    margin: 0;
    color: #969696;
  }
}
.sbl_team {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 90%; //responsive
  row-gap: 50px; //responsive
}
.sbl_teamBox {
  cursor: default;
  position: relative;
  width: 30%; //responsive
  height: 320px; //responsive
  &:hover{
    .sbl_teamBoxN {
      margin-left: 80%; //responsive
      opacity: 1;
    }
    img {
      filter: grayscale(100%);
    }
  }
  img {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    filter: grayscale(0%);
    transition: .3s;
  }
  .sbl_teamBoxN {
    transition: .3s;
    width: 100%;
    height: 100%;
    z-index: 1;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 50%;
    opacity: 0;
    span {
      font-size: 32px; //responsive
    }
    p {
      font-size: 16px; //responsive
    }
  }
}

.sbl_sec5 {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 50px; //responsive
  background: #1b1b1b;
  .sbl_sec5L {
    border-right: 10px solid #f1f1f1;
    text-align: right;
    padding-right: 50px;
  } .sbl_sec5R {
    padding-left: 50px;
    ul {
      justify-content: flex-start;
    }
  }
  .sbl {
    width: 40%;
  }
  img {
    width: 20%;
    padding-top: 10px;
  }
  i, p {
    color: #f1f1f1 !important;
  }
  h1 {
    font-size: 70px;
    color: #9fef00;
    text-decoration: underline;
  }
  .sbl_sec5L, .sbl_sec5R {
    width: 100%;
  }
  ul {
    margin:0;
    padding:0;
    display:flex;
    justify-content: flex-end;
  }
  ul li {
    list-style:none;
  }
  ul li a {
    display:block;
    position:relative;
    width:40px; //responsive
    height:40px; //responsive
    line-height:40px; //responsive
    font-size:20px; //responsive
    text-align:center;
    text-decoration:none;
    color: #f1f1f1;
    transition:.5s;
    margin-right: 20px;
  }
  ul li a span {
    position:absolute;
    transition: transform .5s;
  }
  ul li a span:nth-child(1),
  ul li a span:nth-child(3){
    width:100%;
    height:3px;
    background-color: #f1f1f1;
  }
  ul li a span:nth-child(1) {
    top:0;
    left:0;
    transform-origin: right;
  }
  ul li a:hover span:nth-child(1) {
    transform: scaleX(0);
    transform-origin: left;
    transition:transform .5s;
  }
  
  ul li a span:nth-child(3) {
    bottom:0;
    left:0;
    transform-origin: left;
  }
  ul li a:hover span:nth-child(3) {
    transform: scaleX(0);
    transform-origin: right;
    transition:transform .5s;
  }
  
  ul li a span:nth-child(2),
  ul li a span:nth-child(4){
    width:3px;
    height:100%;
    background: #f1f1f1;
  }
  ul li a span:nth-child(2) {
    top:0;
    left:0;
    transform:scale(0);
    transform-origin: bottom;
  }
  ul li a:hover span:nth-child(2) {
    transform: scale(1);
    transform-origin: top;
    transition:transform .5s;
  }
  ul li a span:nth-child(4) {
    top:0;
    right:0;
    transform:scale(0);
    transform-origin: top;
  }
  ul li a:hover span:nth-child(4) {
    transform: scale(1);
    transform-origin: bottom;
    transition:transform .5s;
  }
  
  .facebook:hover {
    color: #3b5998;
  }
  .facebook:hover span { 
    background: #3b5998;
  }
  .twitter:hover {
    color: #1da1f2;
  }
  .twitter:hover span { 
    background: #1da1f2;
  }
  .instagram:hover {
    color: #c32aa3;
  }
  .instagram:hover span { 
    background: #c32aa3;
  }
  .google:hover {
    color: #dd4b39;
  }
  .google:hover span { 
    background: #dd4b39;
  }
  ul li a .twitter {
    color: #1da1f2;
  }
  ul li a:hover:nth-child(3) {
    color: #c32aa3;
  }
  ul li a:hover:nth-child(4) {
    color: #dd4b39;
  }
}


#loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #1b1b1b;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  div {
    text-align: center;
  }
  img {
    transition: .3s;
    width: 20%;
    opacity: 1;
  }
}

@keyframes sbl_scaling {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

